.menu {
  background: #21304a;
  display: inline-block;
  width: 100%;
  color: #fff;
  font-family: 'Open Sans', sans-serif;
}

.menu > ul {
  padding: 0;
  float: right;
  list-style-type: none;
}

.clear:after {
  content: '';
  clear: both;
}

.menu > ul > li {
  float: left;
  padding: 20px 34px;
  cursor: pointer;
  position: relative;
  font-weight: 400;
}

.menu > ul > li i {
  margin-left: 10px;
}

.menu > ul > li:hover .innerMenu {
  display: block;
}

.innerMenu {
  list-style-type: none;
  position: absolute;
  background: #21304a;
  top: 58px;
  right: 0;
  width: 100%;
  display: none;
}

.innerMenu li {
  padding: 10px 15px;
}

.innerMenu li:hover {
  background: #121a29;
}




